export default {
    data(){
        return {
            children: '',
            phone: '1232233332'
        }
    },
    methods: {
        getChildren(){
            for(let i = 0; i < 3; i++){
                this.children += `
                                <div id="container">
                                    <img id="avator" src="../assets/logo.png" alt=""/>
                                    <div id="info-box">
                                        <span>:</span><br/>
                                        <span>:6</span><br/>
                                        <span>vip:20181230</span>
                                    </div>
                                    <div id="b-vip"></div>
                                </div>
                                `;
            }
        }
    },
    mounted(){
        this.getChildren();
    }
}



