
import XTable from 'vux/src/components/x-table/index.vue'

export default {
components: {
XTable
},
data(){
return {
showLoading: false,
loadingText: '',
index: 0,
group: [],
lifeDays: '20',
lessonCount: '50',
minutes: '200',
lastweekLifeDays: '5',
lastweekLessonCount: '4',
lastweekMinutes: '100',
lastweekMorethan: '96%',
studyProgress: `<tr>
<td></td>
<td id='lesson-name'></td>
<td></td>
</tr>`,
showDetail: false,
studyDetail: `
<tr>
<td></td>
<td></td>
<td></td>
</tr>
`
}
},
methods: {
getGroup(){
this.group.push('');
this.group.push('');
this.group.push('');
},
ongroupClick(index){
this.index = index;
},
onDetailClick(){
this.showDetail  = !this.showDetail;
},
onTaskClick(){
this.showLoading = true;
},
getDetail(){
let arr = [{date:'79', week:'', name: '', time:'11', answerSum: '10', rightSub: '10'},{date:'710', week:'', name: '', time:'11',answerSum: '10', rightSub: '10'},{date:'711', week:'', name: '', time:'11',answerSum: '10', rightSub: '10'}];
arr.forEach((obj)=>{
this.studyProgress += `
<tr>
<td><span>${obj.date}</span><br/><span>${obj.week}</span></td>
<td id='lesson-name'>${obj.name}</td>
<td>${obj.time}</td>
</tr>
`;
this.studyDetail += `
<tr>
<td><span>${obj.date}</span><br/><span>${obj.week}</span></td>
<td id='lesson-name'>${obj.answerSum}</td>
<td>${obj.rightSub}</td>
</tr>
`;
});
}
},
mounted(){
this.getGroup();
this.getDetail();
}
}



